.contact {
    width: 100%;
    padding-top: 60px;
}
.location {
  position: relative;
  background-color: white;
  width: 85%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 50px 0 10px 0;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.locationAnchor {
  position: absolute;
  top: -60px;
}

.locationInfo {
  align-items: center;
}

.location h1 {
  font-size: 32px;
  width: 300px;
  margin: 0 auto;
  text-align: center;
  padding-bottom: 5px;
  border-bottom: 1px solid lightgrey;
}

.locationAddressContainer {
  margin: 0 0 50px 0;
  text-align: center;
}

.locationAddress {
  margin: 20px 0;
}

.locationAddressTitle {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 5px;
}

.locationDirections {
  margin-bottom: 30px;
}

.locationDirectionsTitle {
  font-size: 26px;
  margin: 0 0 30px 0;
  text-align: center;
}

.locationDirIntro {
  font-size: 20px;
  max-width: 800px;
  margin: 0 auto 50px auto;
}

.locationMapsContainer {
  display: grid;
  justify-content: center;
  justify-items: center;
  grid-gap: 50px 20px;
  gap: 50px 20px;
  grid-template-columns: repeat(auto-fill, 400px);
}

.locationDirectionsGraphic {
  position: relative;
  background-color: lightgrey;
  width: 350px;
  height: 350px;
}

.locationDirectionsGraphic img {
  width: 350px;
}

.locationDirGrZoomOverlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: darkgray;
  color: lightgrey;
  opacity: 0.7;
  cursor: pointer;
  display: none;
  text-align: center;
}

.locationDirectionsGraphic:hover .locationDirGrZoomOverlay {
  display: block;
}

.locationDirGrZoomOverlay i {
  margin-top: 100px;
}

.locationMapWrapper {
  width: 350px;
  height: 350px;
}

.locationMap {
  -webkit-filter: saturate(50%);
          filter: saturate(50%);
  height: 350px;
  width: 100%;
}

.locationBigMapLink {
  display: block;
  margin-top: 10px;
  text-align: center;
  font-size: 20px;
  color: #08415c;
}

@media only screen and (max-width: 600px){
  .locationMap{
    margin: 0;
  }
  .locationDirSub h3 {
    font-size: 18px;
  }
}

@media only screen and (max-width: 1000px){
  .locationDirections {
    grid-template-columns: 1fr;
  }
  .locationDirSub {
    width: 100%;
    max-width: 600px;
  }
  .locationDirSub p {
    font-size: 16px;
    font-weight: 400;
  }

  .locationDirIntro p {
    font-size: 16px;
  }
}
.locationDropdown {
    max-width: 800px;
    margin: 0 auto 20px auto;
}

.locationDropdownHeader {
    display: flex;
    align-items: center;
    border-bottom: 1px solid grey;
    padding: 0 10px;
    margin-bottom: 10px;
    cursor: pointer;
}

.locationDropdownHeader h3 {
    font-size: 22px;
    font-weight: 600;
    margin: 10px 0;
    width: 90%;
    text-align: start;
}

.locationDropdownHeader i {
    width: 10%;
    margin-left: 30px;
    text-align: end;
}

.locationDropdownContent {
    transition: max-height 0.5s ease-in-out;
    overflow: hidden;
    width: 80%;
    min-width: 300px;
    margin: 0 auto;
}

.ldcHidden {
    max-height: 0;
}

.ldcOpen {
    max-height: 500px;
}

.locationDropdownContent p {
    text-align: start;
    margin: 0 0 5px 0;
    font-size: 20px;
    font-weight: 400;
}

@media only screen and (max-width: 600px) {
    .locationDropdownContent p {
        font-size: 16px;
    }

    .locationDropdownHeader h3{
        font-size: 20px;
    }
}
.people {
    position: relative;
    padding: 50px 0 0 0;
    z-index: -99;
}

.peopleAnchor {
    position: absolute;
    top: -60px;
}

.peopleTitle {
    text-align: center;
    width: 300px;
    margin: 0 auto;
    font-size: 36px;
    padding-bottom: 5px;
    border-bottom: 1px solid lightgrey;
}

.peopleGrid {
    max-width: 1000px;
    margin: 0 auto;
    background-color: white;
    padding: 20px;
}
.person {
    display: grid;
    grid-template-columns: 300px 270px 360px;
    justify-items: center;
    justify-content: center;
    border-bottom: 1px solid grey;
    margin: 10px 0;
    padding: 50px 0;
}

.person:last-of-type {
    border-bottom: none;
}

.personText {
    text-align: start;
    min-width: 250px;
    margin: 40px 0 0 0px;
    background-color: rgba(255, 255, 255, 0.5);
}

.personText i {
    margin-right: 5px;
}

.personText .fa-mobile-alt {
    margin-right: 12px;
}

.personText h3 {
    font-size: 28px;
    margin: 0 0 20px 0;
}

.personText h4 {
    font-size: 18px;
    margin: 20px 0;
}

.personEmail {
    text-decoration: none;
    color: #08415c;
}

.personEmail:hover {
    color: #4d64c1;
}

.personImg {
    width: 250px;
    height: 300px;
    margin: 10px 0;
    background-color: #ddd;
}

.personImg i {
    color: #aaa;
    margin: 80px 60px;
}

.personQuote {
    display: grid;
    box-sizing: border-box;
    grid-template-columns: 32px 1fr 32px;
    grid-template-rows: 32px 1fr 32px;
    align-items: center;
    background-color: rgba(235, 186, 185, 0.5);
    border-left: 3px solid rgba(8, 65, 92,0.7);
    box-shadow: 2px 4px 3px grey;
    border-radius: 3px;
    width: 320px;
    margin: 20px 0;
    padding: 5px 10px;
    text-align: center;
}

.personQuote p {
    font-size: 18px;
    font-weight: 400;
    font-style: italic;
    grid-column: 1/4;
    grid-row: 2;
}

.personQuote i {
    font-size: 20px;
    color: rgba(8, 65, 92,0.9);
}

.personRightQuote {
    grid-column: 3;
    grid-row: 3;
    justify-self: end;
}


@media only screen and (min-width: 601px) and (max-width: 900px){
    .person {
        grid-template-columns: 300px 270px;
    }
    .personQuote {
        grid-column: 1 / 3;
    }
}

@media only screen and (max-width: 600px){
    .person {
        padding: 20px 0 0 0;
        grid-template-columns: 320px;
    }
    .personQuote {
        margin: 10px 0 20px 0;
    }
    .personText {
        margin-top: 0;
    }
}
